.header-main {
    /*background-color: rgba(4, 15, 15, .9);*/
    background-color: rgba(1, 60, 54, .9);
    margin: 10px 40px;
    width: auto;
    color: aliceblue;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 100px;
    border-radius: 5px;
    border: solid 2px #040F0F;
}

.header {
    display: flex;
    justify-content: space-around;
    padding: 0 20px;
}

.header-historias {
    flex-direction: column;
    align-items: flex-end;
}

.header-left {
    padding: 20px 0 0 0;
}

.header-search {
    width: 200px;
    margin: 0 20px;
    background-color: inherit;
    color: aliceblue;
    font-size: 1em;
    outline: none; /*Retira o focus do input quando selecionado, evitar de aparecer as bordas*/
    border: none; /*Retira as bordas do input*/
    border-bottom: solid 1px aliceblue; /*Coloca borda somente na parte de baixo*/   
}
/*Código para alterar a cor do placeholder*/
.header-search::-webkit-input-placeholder {
    color: var(--text-color);
}
.header-search:-moz-placeholder {
    color: var(--text-color);
}
.header-search::-moz-placeholder {
    color: var(--text-color);
}
.header-search:-ms-input-placeholder {  
    color: var(--text-color);
}

.header-buttons {
    display: flex;
    width: 100%;
    justify-content: space-around;
}

.header-button {
    margin: 0 10px;
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.active {
    background: rgba(1, 60, 54, .9);
    font-size: 1.2em;
    padding: 5px 15px;
    border-radius: 5px;
}

.desactive {
    background: none;
    font-size: 1em;
    padding: 0;
}

@media (max-width: 1000px) {
    .header-main {
    }
}

@media (max-width: 700px) {
    .header-main {
        margin: 0;
        height: 200px;
        width: 100%;
    }
}
.box-card {
    background-color: rgba(24, 97, 89, .9);
    /*width: 30%;*/
    margin: 5px;
    padding: 15px;
    border-radius: 5px;
    cursor: pointer;
    border: solid 1px #040F0F;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.row-card p {
    margin: 5px;
    color: var(--text-color);
    text-align: justify;
}

.row-card-custom {
    display: flex;
    justify-content: space-between;
}

.icone-row-card {
    width: 16px;
    cursor: pointer;
    padding: 0 5px;
    text-decoration: none;
    color: black;
}


@media (max-width: 1000px) {
    .box-card {
        /*width: 45%;*/
        /*margin: 10px 0 0 0;*/
    }
}

@media (max-width: 700px) {
    .box-card {
        /*width: 90%;*/
        /*margin: 10px auto 0;*/
    }
}

.buttonCustom {
    background-color: var(--background-color);
    color: var(--text-color);
    padding: 10px 20px;
    margin: 5px 10px;
    box-sizing: border-box;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.main-wrapper {
    background-image: url(/NarrativeText-Frontend/static/media/blank-business-composition-1280.d04236e8.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    display: flex;
    flex-flow: column nowrap;
}

.main-cards {
    /*display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 40px;*/
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    justify-content: space-between;
    margin: 10px 40px;
    width: auto;
}

.actions {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 10px;
}

.grey {
    background-color: rgba(105,105,105,.9);
    display: none;
}


@media (max-width: 1000px) {

}

@media (max-width: 700px) {
    .main-wrapper {
        background-image: none;
        background-color: aliceblue;
        width: 100%;
    }
    
    .main-cards {
        padding: 0;
    }
}
.alternativeText-list {
    display: block;
    background-color: rgba(0,0,0,0);
    color: var(--background-color);
    margin: 10px 20px;
    box-sizing: border-box;
    border: none;
    cursor: pointer;
    font-size: 1em;
    text-align: justify;
}

.alternativeText-p {
    text-align: center;
}
.read-wrapper {
    background-image: url(/NarrativeText-Frontend/static/media/art-artist-background-1280.369a3fa9.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
}

.p-editor-toolbar, .p-editor-content, .read-header, .read-box {
    width: 90%;
    margin: 0 auto;
    background-color: rgb(240, 248, 255);
}

.p-editor-content, .read-box {
    height: 60vh;
    background-color: rgba(240, 248, 255, .9);
}

.read-box {
    border: solid 1px #3d3d3d;
    border-radius: 5px;
    padding: 10px;
    overflow:scroll;
}

.read-header {
    display: flex;
    flex-direction: row;
    background-color: rgba(240, 248, 255, .0);
    justify-content: space-between;
}

.read-header-rigth {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.item-read-header {
    padding: 10px 15px;
}

.item-read-header:first-child {
    padding: 20px 5px 5px 15px;
}

.item-read-header:last-child {
    padding: 5px 5px 20px 15px;
}

.icone-read-wrapper {
    width: 20px;
    cursor: pointer;
    padding: 0 10px;
    text-decoration: none;
    color: black;
}

.icone-read-wrapper p {
    min-width: 0;
    min-width: initial;
}

.icone-read-wrapper-custom img {
    padding: 0 0 0 5px;
}

.icone-read-wrapper-custom {
    padding: 0 10px;
    font-size: 1.5em;
}

.read-header-actions {
    display: flex;
    padding: 15px;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
}

.read-header-alternativeText {
    padding: 15px;
    width: 90%;
    margin: 0 auto;
} 

.read-header-alternativeLink {
    display: flex;
    text-decoration: none;
    color: blue;
    font-size: .9em;
    cursor: pointer;
    height: 20px;
}

.read-header-alternativeLink p {
    padding: 0 20px;
}

.read-header-alternativeLink span {
    padding: 0 5px;
}

.read-header-alternativeLink img {
    width: 16px;
    height: 16px;
}

